  import urls from '../../../utils/urls.js'
  export default {
    data () {
      return {
        isSure: false,
        second: 60,
        showText: true,
        phone: '',
        psdErr: false,
        isRequest: false,
				isLogin: this.$store.state.isLogin
      }
    },
    methods: {
      checkPhone: function (e) {
        this.phone = e.detail.value
      },
      getIdenCode: function (e) {
        if (e.detail.value.length >= 4) {
          this.showText = true
          this.isSure = true
          this.second = 60
        } else {
          uni.showToast({
            title: '请填写正确的验证码',
            icon: 'none'
          })
        }
      },
      getCode: function () {
        var reg = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/
        var phone = this.phone
        if (!reg.test(phone)) {
          uni.showToast({
            title: '请输入正确的手机号',
            icon: 'none'
          })
        } else {
          this.showText = false
          var interval = setInterval(() => {
            --this.second
          }, 1000)
          uni.request({
            url: urls.HTTP + '/user/getmcodenum',
            data: {
              username: phone
            }
          })
          setTimeout(() => {
            clearInterval(interval)
            this.showText = true
          }, 60000)
        }
      },
      submit: function (e) {
        // var id = uni.getStorageSync('user').id
				// let id = JSON.parse(uni.getStorageSync('userInfo')).id
				let id = '';
				if(this.isLogin){
					id = JSON.parse(uni.getStorageSync('userInfo')).id
				}else{
					 id = 0
				}
        if (!e.detail.value.phone || !e.detail.value.code) {
          uni.showToast({
            title: '信息不能为空',
            icon: 'none'
          })
        } else {
          this.isRequest = true
          uni.request({
            url: urls.HTTP + '/user/checkmobile',
            method: 'POST',
            data: {
              mobile: e.detail.value.phone,
              code: e.detail.value.code,
              id: id
            },
            success: (res) => {
              this.isRequest = false
              console.log(res)
              if (res.data.code === 1) {
                uni.showToast({
                  title: res.data.error,
                  icon: 'none'
                })
              } else {
                uni.showToast({
                  title: res.data.error
                })
                uni.redirectTo({
                  url: 'resetPsdTwo'
                })
              }
            }
          })
        }
      }
    }
  }
